.list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-radius: 4px;
}

.list-item {
  background-color: white;
}

.list-item:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.list-item:hover {
  background-color: #D5D7E5;
}

