.shadow-div {
  position: absolute;
  width: 380px;
  height: 50px;
  z-index: -1;
  left: 10px;
  right: 10px;
  top: 5px;
  background-color: #D5D7E5;
  border-radius: 4px;
  opacity: 0.7;
}

.list-open {
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
