.popup {
    position:fixed;
    top:0;
    bottom:0;
    left:0;
    right:0;
    overflow-x:hidden;
    overflow-y:auto;
    -webkit-overflow-scrolling:touch;
    z-index:999;
    width:0;
    height:0;
    opacity:0;
}
.popup:target {
	width:auto;
	height:auto;
	opacity:1;
	-webkit-transition:  opacity 1s ease;
	transition: opacity 1s ease;
}
.popup:target .popup-overlay {
	position:fixed;
	top:0;
	bottom:0;
	left:0;
	right:0;
	cursor:pointer;
	background-color:#000;
	background-color:rgba(0, 0, 0, 0.7);
	z-index:1;
}
.popup .popup-dialog {
	border-radius:6px;
	box-shadow:0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
	position:relative;
	/* width: 90%; */
	max-width:660px;
	max-height:70%;
	margin: 50px auto;
	overflow-x:hidden;
	overflow-y:auto;
	z-index:2;
}
.popup-dialog-lg {max-width:820px !important;}

.popup .popup-content {background:#fff; padding:23px 27px;}
.popup .popup-close {
    position:absolute;
	top:13px;
	right:13px;
	color:#0085a6;
	background-color:#fff;
	border-radius:50%;
	height:40px;
	width:40px;
	font-size:30px;
	line-height:37px;
	text-align:center;
	-webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}
.popup .popup-close:hover {background-color:#0085a6; color:#fff; cursor:pointer;}



