.pagination-wrapper {
    width: 100%;
    height: 50px;
    margin-bottom: 10px;
    text-align: center;
}

.button-prev {
    cursor: pointer;
    margin-right: 40px;
    height: 100%;
    font-size: 1.4em;
    font-weight: bold;
    padding: 8px;
    background-color: white;
    color: #2A2B8D;
    border: 3px solid #2A2B8D; 
    border-radius: 20%;
}

.button-next {
    cursor: pointer;
    margin-left: 40px;
    height: 100%;
    font-size: 1.4em;
    font-weight: bold;
    padding: 8px;
    background-color: white;
    border: 3px solid #2A2B8D;
    color: #2A2B8D; 
    border-radius: 20%;
}
  
.button-next:hover, .button-prev:hover {
    color: #7FD6F2;
    border-color: #7FD6F2;
}   

.button-next:hover:disabled, .button-prev:hover:disabled {
    cursor: auto;
    color: #2A2B8D; 
    border-color: #2A2B8D; 
}
